import { Container, Row, Col } from 'react-bootstrap';
import ProfilePic from "../assets/images/luis.jpg";

const About = () => {
  return (
    <div id="about">
      <Container fluid="sm" className="about_box">
        <Row className="justify-content-md-center">
          <Col sm={4}>
            <img className="profile_pic" src={ProfilePic} alt="Luís Macedo" />
          </Col>
          <Col sm={8} className="about_description">
            <h2>About Me</h2>
            <p>
              My name is Luís Macedo and Im a software engeneer currently working in my masters thesis about security in robots with ARM cpus.
            </p>
          </Col>
        </Row>
        <Row className="justify-content-md-center">
          <Col sm></Col>
          <Col sm>Contacts</Col>
          <Col sm>Download CV</Col>
        </Row>
        </Container>
    </div>
  );
}

export default About;